// -----------------------------------------------------------------------------
// This file contains styles that are specific to the home page.
// -----------------------------------------------------------------------------

// Main Section
.main {
  height: 1236px;
  position: relative;
}
.objects {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  &__1 {
    position: absolute;
    height: 200rem;
    width: 200rem;
    top: 0;
    right: 0;
    overflow: hidden;
    .elem {
      position: absolute;
      right: -30%;
      top: -27%;
      height: 150rem;
      width: 150rem;
      border-radius: 50%;
      background-color: $main-red-color;

      @media screen and (max-width: 1440px) {
        right: -33%;
        top: -38%;
      }
    }
  }

  &__2 {
    position: absolute;
    height: 60rem;
    width: 81rem;
    top: 0;
    right: 0;
    overflow: hidden;
    z-index: 5;
    .elem {
      position: absolute;
      right: -88%;
      top: -152%;
      height: 150rem;
      width: 150rem;
      border-radius: 50%;
      background-color: rgba(#f6c3ca, 0.8);

      @media screen and (max-width: 1440px) {
        right: -119%;
        top: -169%;
      }
    }
  }

  &__3 {
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url(../img/objects/header-object.svg);
    background-repeat: no-repeat;
    // background-position: -94rem -166rem;
    // background-position: -131rem -166rem;
    background-position: 82% 90%;

    @media screen and(max-width: 1440px) {
      background-position: 81% 100%;
    }
  }
}

.slider {
  position: relative;
  z-index: 9;
  &__title {
    // font-size: 20rem;
    font-size: 10.5vw;
    font-weight: 700;
  }

  &__text {
    color: $text-grey-color;
    max-width: 70rem;
    // font-size: 24px;
    font-size: 1.25vw;
    line-height: 1.65;
    // margin-top: 6rem;
    margin-top: 6vh;
    // margin-bottom: 12rem;
    margin-bottom: 12vh;
  }

  &__pagination {
    margin-top: 8vh;
    .swiper-pagination-bullet {
      width: 12px;
      height: 12px;
      background-color: $slider-bullet-grey;

      & + .swiper-pagination-bullet {
        margin-left: 22px;
      }

      &-active {
        background-color: #fff;
        box-shadow: 0 0 0 3px $main-red-color;
      }
    }
  }
}

.social {
  position: absolute;
  right: -8rem;
  top: 55vh;
  transform: rotate(-90deg);
  display: flex;

  z-index: 100;

  &__title {
    font-size: 1.4rem;
    color: #fff;
  }

  &__icons {
    display: flex;
    justify-content: space-between;
    width: 12rem;
    margin-right: 3rem;
  }

  &__icon {
    width: 2rem;
    height: 2rem;
    fill: #fff;
    transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.8);

    &:hover {
      transform: scale(1.5);
      transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.8);
    }
  }

  &__link {
    width: 2rem;
    height: 2rem;
    cursor: pointer;
    transform: rotate(90deg);
  }
}

// About Section
.about {
  position: relative;
  margin-top: 20rem;

  .container {
    display: flex;
  }
  &__cards {
    flex: 0 0 50%;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    .cards-group {
      display: flex;
      flex-direction: column;
      margin-right: 9rem;
    }

    .card {
      background-color: #fff;
      border-radius: 12px;
      box-shadow: 0 3px 20px rgba(#7699fc, 0.1);
      width: 28rem;
      min-height: 38rem;
      padding: 4rem;
      padding-left: 5rem;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;

      &__image {
        width: 10rem;
      }

      &__title {
        color: $text-secondary-color;
        font-size: 1.8rem;
        margin-top: 5.5rem;
      }

      &__text {
        color: $text-grey-color;
        font-size: 1.6rem;
        line-height: 1.5;
        margin-top: 2rem;
      }

      & + .card {
        margin-top: 9rem;
      }
    }
  }

  &__info {
    flex: 0 0 50%;
    padding-left: 20rem;
    padding-top: 10rem;
  }

  &__title {
    font-size: 4rem;
    max-width: 50rem;
  }

  &__text {
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 1.875;
    color: $text-grey-color;
    max-width: 50rem;
    margin-top: 6rem;
    margin-bottom: 10rem;
  }

  &__object {
    background-image: url(../img/objects/about-object.svg);
    background-repeat: no-repeat;
    background-size: contain;
    width: 1800px;
    height: 1800px;
    // width: 1600px;
    // height: 1600px;
    position: absolute;
    // left: -595px;
    right: 400px;
    top: -460px;
    z-index: -1;
  }
}
